/* Background Styling */
.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: none;
  z-index: -1;
}
/* Position the Back button at the top left corner */
.back-button {
  position: absolute;
  top: 15px;
  left: 15px;
  padding: 10px 20px;
  background-color: #cccccc;
  color: #000;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  z-index: 10; /* Ensure it's above other elements */
}

.back-button:hover {
  background-color: #bbbbbb;
}
/* Container for the Form */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.form-container {
  background-color: #ffffff;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 2rem;
  text-align: center;
  width: 400px;
}

.form-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

/* Form Inputs */
.form-group {
  margin-bottom: 1.2rem;
  text-align: left;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  color: #333;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="password"] .form-group input[type="tel"] {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  outline: none;
  box-sizing: border-box;
}

.form-group input:focus {
  border-color: #79a7ff;
}

/* Checkbox Group */
.checkbox-group {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: #333;
}

.checkbox-group input {
  margin-right: 0.5rem;
}

/* Submit Button */
.submit-button {
  background-color: black;
  color: white;
  border: none;
  padding: 0.8rem;
  width: 100%;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #333;
}

/* Sign-in Button */
.sign-in-button {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: 1px solid #333;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  border-radius: 4px;
  cursor: pointer;
}

.sign-in-button:hover {
  background-color: #f4f4f4;
}
